<template>
    <div class="route-login">
        <div class="cmn-center-middle login-form">
            <div>
                <q-input v-model="username" float-label="请输入姓名"  />
            </div>
            <div>
                <q-input v-model="mobile" float-label="请输入手机号"  />
            </div>
            <div>
                <q-btn color="primary" :loading="loading" @click="login">登录</q-btn>
            </div>
            <div class="tip">
                温馨提示：如果提示未匹配到信息，请确认手机号和姓名是否正确或前往物业进行信息登记
            </div>
        </div>
    </div>
</template>

<script>
    import {type} from '../../util'
    const {isFalse} = type

    export default {
        data(){
            return {
                username:'',
                mobile:'',
                loading:false
            }
        },
        methods:{
            login(){
                if(!isFalse(this.username)){
                    this.$q.notify({ color: 'negative', message: '请填写姓名!',timeout:100 })
                    return
                }
                this.$router.push({path:'/main'})
            }
        }
    }
</script>

<style lang="scss" >
    .route-login{
        position: relative;
        .login-form{
            width: 90vw;
            // border: 1px solid var(--q-color-primary);
            border-radius: 5px;
            padding: 15px;
            padding-bottom: 45px;
            >div{
                padding: 5px 0;
                &.tip{
                    margin-top: 40px;
                    font-size: 14px;
                    color: #bbb;
                }
                >button{
                    width: 100%;
                    margin-top: 30px;
                    padding: 15px 0;
                    font-size: 16px;
                }
            }
        }
    }
</style>